body {
  margin: 0;
}

.container {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__hand-minute {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin-left: -1.5vmin;
  animation: 3600s steps(60, end) infinite rotate-hand;
}

.stopwatch__hand-second {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  margin-left: -.75vmin;
  animation: 61s linear infinite rotate-hand;
}

.stopwatch__hand-minute, .stopwatch__hand-second {
  transform-origin: 50% 100%;
  position: absolute;
  bottom: 50%;
  left: 50%;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotate-hand {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch--speed-up .stopwatch__hand-second {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__hand-minute {
  animation-duration: 600s;
}
